.footer {
    background-color: $skirret;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
}

.footer__navbar-brand {
    color: $black;
    font-size: 25px;
    &:hover {
        color: $black;
    }
}

.footer__form-control {
    border-left: 2px solid $dark-white;
    border-bottom: 2px solid $dark-white;
    border-top: 2px solid $dark-white;
    border-right: none;
    position: relative;
    border-bottom-left-radius: 25px;
    border-top-left-radius: 25px;
    padding: 10px 20px;
    outline: none;
    background-color: $skirret;
    color: $dark-white;
    width: 78%;
}

.footer__title {
    color: $white;
    font-weight: bold;
    text-transform: uppercase;
}

.fa-facebook-f {
    color: $white !important;
    font-weight: bold;
    padding: 2px;
    border-radius: 100%;
    border: 1px solid $white;
    height: 50px;
    width: 50px;
    padding-left: 12px;
    padding-top: 9px;
}

.fa-basketball-ball {
    color: $white !important;
    font-weight: bold;
    padding: 2px;
    border-radius: 100%;
    border: 1px solid $white;
    height: 50px;
    width: 50px;
    padding-left: 8px;
    padding-top: 8px;
}

.fa-twitter,
.fa-instagram,
.fa-share-alt-square {
    color: $white !important;
    font-weight: bold;
    padding: 2px;
    border-radius: 100%;
    border: 1px solid $white;
    height: 50px;
    width: 50px;
    padding-left: 9px;
    padding-top: 9px;
}

.footer__navbar>.nav-item>.nav-link {
    color: $white !important;
    padding: 1px 0px;
    font-weight: bold;
}