// Required Styles
@mixin star-empty() {
    &:before {
        content: url(./../../img/star.svg);
    }
}

@mixin star-full() {
    &:before {
        content: url(./../../img/star1.svg);
    }
}

.rating {
    position: relative;
    overflow: hidden;
    label {
        display: inline-block;
        vertical-align: middle;
        cursor: pointer;
        @include star-full;
    }
    input {
        position: absolute;
        top: 0;
        left: -50px;
        &:checked {
            +label {
                background-color: $dark-white;
                @include star-full;
            }
            ~label {
                background-color: $dark-white;
                @include star-empty;
            }
        }
    }
    &:hover {
        >input {
            +label {
                @include star-full;
                &:hover {
                    ~label {
                        @include star-empty;
                    }
                }
            }
        }
    }
}