.banner-inner>.carousel-item {
    max-height: 400px;
}

.banner-title {
    font-weight: bolder;
    font-size: 50px;
    color: $green;
    text-shadow: -1px 0 #000, 0 1px #000, 1px 0 #000, 0 -1px #000;
}

.banner-subtitle {
    font-weight: bolder;
    font-size: 35px;
    color: $green;
    text-shadow: -1px 0 #000, 0 1px #000, 1px 0 #000, 0 -1px #000;
}

.carousel-inner {
    max-width: 100%!important;
    margin: 0 auto;
}

.arrow-icon {
    width: 120px;
    height: 120px;
}

.banner-img {
    background-position: center center;
}

.carousel-indicators li {
    -webkit-box-sizing: content-box;
    box-sizing: content-box;
    -webkit-box-flex: 0;
    -ms-flex: 0 1 auto;
    flex: 0 1 auto;
    width: 25px;
    height: 25px;
    border-radius: 100%;
    margin-right: 3px;
    margin-left: 3px;
    text-indent: -999px;
    cursor: pointer;
    background-color: $dark-green;
    background-clip: padding-box;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    opacity: .5;
    -webkit-transition: opacity .6s ease;
    -o-transition: opacity .6s ease;
    transition: opacity .6s ease;
}

.carousel-caption {
    position: absolute;
    right: 15%;
    bottom: 100px !important;
    left: 15%;
    z-index: 10;
    padding-top: 20px;
    padding-bottom: 20px;
    color: #fff;
    text-align: center;
}

.section-title {
    color: $black;
    font-weight: bolder;
    padding: 30px 0px 10px 0px;
}

.category-section-title,
.category-link {
    color: $black;
    font-weight: bolder;
    padding: 40px 0px;
}

.category-section-title {
    font-size: 30px;
}

.tea-category,
.gift-category,
.plate-category,
.sets-category,
.sweets-category,
.coffee-category {
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 300px;
    background-position: center center;
    border-radius: 15px;
    margin: 20px 10px;
    box-shadow: 0px 3px 18px 0px rgba(0, 0, 0, 0.75);
    text-shadow: 2px 0 0 #fff, -2px 0 0 #fff, 0 2px 0 #fff, 0 -2px 0 #fff, 1px 1px #fff, -1px -1px 0 #fff, 1px -1px 0 #fff, -1px 1px 0 #fff;
}

.tea-category {
    background-image: url('./../../img/category/tea.jpg');
}

.gift-category {
    background-image: url('./../../img/category/gift.jpg');
}

.plate-category {
    background-image: url('./../../img/category/plate.jpg');
}

.sets-category {
    background-image: url('./../../img/category/sets.jpg');
}

.sweets-category {
    background-image: url('./../../img/category/sweets.jpg');
}

.coffee-category {
    background-image: url('./../../img/category/coffee.jpg');
}

.card {
    border-radius: 15px;
    margin: 15px 10px;
    box-shadow: 0px 3px 18px 0px rgba(0, 0, 0, 0.75);
    text-shadow: 2px 0 0 #fff, -2px 0 0 #fff, 0 2px 0 #fff, 0 -2px 0 #fff, 1px 1px #fff, -1px -1px 0 #fff, 1px -1px 0 #fff, -1px 1px 0 #fff;
}

.fa-heart {
    color: $grey;
    transition: all 0.5s;
    &:hover {
        color: $cherry;
    }
}

.card-img-top {
    width: 40% !important;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.subscribe-section {
    background-image: url('./../../img/banner4.jpg');
    background-position: center center;
    background-size: cover;
    min-height: 400px;
}

.subscribe__title,
.subscribe__text,
.form-check-label {
    font-weight: bolder;
}

.subscribe__subtitle {
    font-weight: bolder;
    color: $green;
    text-shadow: -1px 0 #000, 0 1px #000, 1px 0 #000, 0 -1px #000;
}

.subscribe-form-control {
    border-left: 1px solid $grey;
    border-bottom: 1px solid $grey;
    border-top: 1px solid $grey;
    border-right: none!important;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
    padding: 25px 15px;
    outline: none!important;
    &:focus {
        outline: none!important;
    }
}

.btn-subscribe {
    @include btn-main($skirret,
    $white,
    16px);
    padding: 10px 15px;
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
    border: none;
    outline: none!important;
    &:focus,
    &:active {
        outline: none!important;
    }
    &:hover {
        background: $dark-green;
    }
}

.card-post {
    border-radius: 15px;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
    margin: 20px 10px;
    box-shadow: 0px 3px 18px 0px rgba(0, 0, 0, 0.75);
    border: none;
}

.card-post-text {
    font-size: 0.8vw;
}

