html {
    scroll-behavior: smooth;
}

p {
    margin-bottom: 0.3rem;
}

ul {
    list-style-type: none!important;
    margin: 0!important;
    padding: 0!important;
}

a {
    text-decoration: none !important;
}

body {
    font-family: "PlayfairDisplay-VariableFont_wght";
}

.nav-link {
    display: block;
    padding: 0rem;
    color: $black;
}

@font-face {
    font-family: "PlayfairDisplay-VariableFont_wght";
    src: url("/fonts/PlayfairDisplay-VariableFont_wght.woff2") format("woff2"), url("/fonts/PlayfairDisplay-VariableFont_wght.woff") format("woff");
}

.body-wrapper {
    margin: 2%;
    border-radius: 25px !important;
    -webkit-box-shadow: 0px 8px 24px -2px rgba(113, 116, 130, 1);
    -moz-box-shadow: 0px 8px 24px -2px rgba(113, 116, 130, 1);
    box-shadow: 0px 8px 24px -2px rgba(113, 116, 130, 1);
}