.btn-main {
    @include btn-main($skirret, $white, 16px);
    font-weight: bolder;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-radius: 18px;
    box-shadow: 0px 8px 24px -2px rgba(113, 116, 130, 1);
    padding: 5px 12px;
    outline: none!important;
    &:focus {
        outline: none!important;
    }
    &:hover {
        background: $dark-green;
    }
}

.btn-image {
    margin: 0px 5px;
    height: 40px;
    width: 40px;
}

.btn-sort {
    @include btn-main($skirret, $white, 16px);
    font-weight: bolder;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 10px 5px;
    box-shadow: 0px 8px 24px -2px rgba(113, 116, 130, 1);
    padding: 5px 12px;
    outline: none!important;
    &:focus {
        outline: none!important;
    }
    &:hover {
        background: $dark-green;
    }
    margin: 0px 30px;
}

.btn-like {
    border: none;
    background-color: transparent;
    outline: none!important;
}

.heart-icon {
    position: relative;
    top: 38%!important;
    right: 1%;
    margin-top: 5px;
}

.btn-account {
    @include btn-main($green, $white, 16px);
    font-weight: bolder;
    border: 1px solid $black;
    padding: 10px 15px;
    &:hover {
        color: $white;
    }
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-family: "PTMono";
    border-radius:20px;
}

.btn-card {
    @include btn-main($white, $white, 16px);
    border: 2px solid $light-green;
    border-radius: 15px;
    &:hover {
        border: 2px solid $green;
        background-color: $midnight;
    }
}

.btn-banner {
    @include btn-main($orange, $white, 16px);
    font-weight: bolder;
    border: 1px solid $grey;
    border-radius: 5px;
    padding: 10px;
    text-transform: uppercase;
    font-family: "PTMono";
    &:hover {
        color: $black;
    }
}

.btn-form {
    @include btn-main($green, $white, 14px);
    padding: 8px;
    font-weight: bolder;
    border: 1px solid $grey;
    border-radius: 5px;
    outline: none;
    font-family: "PTMono";
    &:hover {
        color: $white;
        box-shadow: 2px 4px $grey;
        border: 1px solid $grey;
        border-radius: 5px;
    }
}

.sort-btn {
    padding: 10px;
    padding-left: 35px;
    border-radius: 20px;
    background-color: $white;
    border: 1px solid $grey;
    outline: none!important;
    -webkit-appearance: none;
    -moz-appearance: none;
    background: transparent;
    background-image: url(./../../img/arrow-bottom.svg);
    background-repeat: no-repeat;
    background-position-x: 90%;
    background-position-y: 10px;
    box-shadow: 1px 1px rgb(201, 201, 201);
}

.btn-order {
    @include btn-main($green, $white, 16px);
    font-weight: bolder;
    padding: 15px 30px;
    border-radius: 40px;
    -webkit-box-shadow: 0px 8px 24px -2px rgba(113, 116, 130, 1);
    -moz-box-shadow: 0px 8px 24px -2px rgba(113, 116, 130, 1);
    box-shadow: 0px 8px 24px -2px rgba(113, 116, 130, 1);
    &:hover {
        border-radius: 40px !important;
        outline: none !important;
        color: $white;
    }
    &:active {
        border-radius: 40px !important;
        outline: none !important;
    }
    outline:none !important;
}

.page-link {
    @include btn-main($green, $white, 16px);
    font-weight: bolder;
    font-family: "PTMono";
}

.btn-scroll {
    position: relative;
    top: -20px;
    right: 20px;
    border: 1px solid $midnight;
    padding: 6px;
    background-color: $midnight;
    border-radius: 100% !important;
    overflow: hidden!important;
    outline: none !important;
}

.btn-search {
    background-color: $white;
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
    border-top: 2px solid $grey;
    border-right: 2px solid $grey;
    border-bottom: 2px solid $grey;
    border-left: none;
    padding: 12px;
    outline: none!important;
    &:focus {
        outline: none!important;
    }
}

.subscribe-btn {
    background-color: $white;
    width: 48px;
    height: 48px;
    border: none;
    border-radius: 100%;
    position: relative;
    right: 21px;
}

.close-icon {
    height: 30px;
    width: 30px;
}