.register-form,
.login-form {
    margin: 40px 15%;
    padding: 70px 30px;
    border: 1px solid $black;
    border-radius: 20px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: right center;
    -webkit-box-shadow: 0px 5px 13px 3px rgba(8, 3, 26, 1);
    -moz-box-shadow: 0px 5px 13px 3px rgba(8, 3, 26, 1);
    box-shadow: 0px 5px 13px 3px rgba(8, 3, 26, 1);
}

.register-form {
    background-image: url(../../img/bg6.jpg);
}

.login-form {
    background-image: url(../../img/bg7.jpg);
}

.account-text {
    text-shadow: 1px 1px $dark-white;
}

.social-icon {
    background-color: #306ba6;
    padding: 10px 11px;
    border: 1px solid #124578;
    border-radius: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    -webkit-box-shadow: 1px 1px 6px 6px #464242;
    box-shadow: 1px 1px 6px 6px #677cf6;
    margin-top: 5px;
    margin-bottom: 5px;
}

.social-icon1 {
    background-color: #306ba6;
    padding: 10px 16px;
    border: 1px solid #124578;
    border-radius: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    -webkit-box-shadow: 1px 1px 6px 6px #464242;
    box-shadow: 1px 1px 6px 6px #677cf6;
    margin-top: 5px;
    margin-bottom: 5px;
}