.breadcrumb {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    padding: .75rem 1rem;
    margin-bottom: 1rem;
    list-style: none;
    background-color: transparent!important;
    border-radius: .25rem;
}

.breadcrumb-item>a {
    color: $black;
    font-weight: bolder;
}

.aside {
    background-color: $white;
    padding: 20px 30px;
}

.line {
    border-bottom: 2px solid $light-blue;
    margin-bottom: 20px;
    margin-left: 5%;
    width: 100%;
}

.custom-range {
    max-width: 100%!important;
    height: 1.4rem;
    padding: 0;
    background-color: transparent;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.max-price {
    position: relative;
    left: -3px;
}

.custom-range::-webkit-slider-thumb {
    background: $dark-green;
    padding: 10px;
    -webkit-box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.75);
    &:hover {
        background: $dark-green !important;
    }
    &:active {
        background: $dark-green !important;
    }
    &:visited {
        background: $dark-green !important;
    }
    &:checked {
        background: $dark-green !important;
    }
}

.custom-range::-moz-range-thumb {
    background: $dark-green;
    padding: 10px;
    -webkit-box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.75);
    &:hover {
        background: $dark-green !important;
    }
    &:active {
        background: $dark-green !important;
    }
    &:visited {
        background: $dark-green !important;
    }
    &:checked {
        background: $dark-green !important;
    }
}

.custom-range::-ms-thumb {
    background: $dark-green;
    padding: 10px;
    -webkit-box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.75);
    &:hover {
        background: $dark-green !important;
    }
    &:active {
        background: $dark-green !important;
    }
    &:visited {
        background: $dark-green !important;
    }
    &:checked {
        background: $dark-green !important;
    }
}

.container {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 30px;
    width: 30px;
    background-color: $white;
    border-radius: 8px;
    border: 1px solid $grey;
    margin-right: 5px;
}

.container:hover input~.checkmark {
    background-color: $white;
}

.container input:checked~.checkmark {
    background-color: $white;
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

.container input:checked~.checkmark:after {
    display: block;
}

.container .checkmark:after {
    left: 9.5px;
    top: 4px;
    width: 12px;
    height: 18px;
    border: 1px solid $skirret;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

.checkbox-text {
    font-size: 16px;
    font-weight: bolder;
    padding-left: 5px;
}

.top-bar {
    background-color: $white;
}

.pagination>li>a {
    border-radius: 50% !important;
    margin: 0 5px;
    background-color: $skirret;
    height: 50px;
    width: 50px;
    padding-top: 15px;
}

.page-item.active .page-link {
    z-index: 3;
    color: #fff;
    background-color: $midnight;
    border-color: $midnight;
    &:hover {
        background-color: $midnight;
        border-color: $midnight;
        color: $white!important;
    }
}

.page-link {
    &:hover {
        background-color: $midnight;
        border-color: $midnight;
        color: $white!important;
    }
}