.cart-title {
    font-size: 18px;
    font-weight: bolder;
}

.cart-price {
    font-size: 18px;
    padding-top: 21px;
    font-weight: bolder;
}

.price-higlight {
    text-decoration: underline;
    padding-left: 5px;
}

.cart-img {
    width: 100px;
}

.qty-iput {
    overflow: hidden;
}

.btn-cancel {
    margin-top: 16px;
}

.cancel-icon {
    position: relative;
    bottom: 7px;
    right: 8px;
}

.product-cart-block,
.total-price-info {
    border-radius: 15px!important;
    box-shadow: -1px 2px 4px 1px rgba(0, 0, 0, 0.75);
}

.value-button {
    height: auto;
    width: 50px;
    border: none!important;
    text-align: center;
    font-size: 20px;
    font-weight: bolder;
    cursor: pointer;
}

.btn-cancel {
    border: 1px solid $cherry;
    border-radius: 100%;
    padding: 10px;
    outline: none;
    background-color: $white;
}

.product-cart-block {
    border-radius: 15px!important;
    box-shadow: -1px 2px 4px 1px rgba(0, 0, 0, 0.75);
}